<template>
  <div class="address">
    <van-nav-bar title="收货地址" left-arrow @click-left="$router.go(-1)" />
    <!-- 地址主体 -->
    <div class='address-body' v-if="isLogin && addressList.length > 0">
      <!-- 开头 -->
      <div class="address-title">
        <span class="all">共<i>{{ addressList.length || 0 }}</i>条地址</span>
        <span @click="addAddressWindow">
          <van-icon name="add" />
          添加
        </span>
      </div>
      <!-- 列表 -->
      <div class="address-list">
        <div class="address-item" v-for="item in addressList" :key="item.address_id">
          <div class="address-body" @click="useAddress(item.address_id)">
            <div class="top">
              <span class=name>姓名：{{ item.name }}</span>
              <span class="phone">手机号：{{ item.phone }}</span>
            </div>
            <div class="middle">
              <div class="province"><span>省份：{{ item.region.province }}</span></div>
              <div class="city"><span>城市：{{ item.region.city }}</span></div>
              <div class="region"><span>县区：{{ item.region.region }}</span></div>
            </div>
          </div>
          <div class="bottom">
            <div class="detail"><span>详细地址：{{ item.detail }}</span></div>
            <span class="delete" @click="handleDel(item.address_id)"><van-icon name="delete" />删除</span>
          </div>
        </div>
      </div>
    </div>
    <!-- 地址为空 -->
    <div class="empty-address" v-else>
      <div class="tips">
        地址为空
      </div>
    </div>
    <!-- 弹窗 -->
    <van-action-sheet v-model="showPannel" title="新增地址">
      <div class="addressinp">
        <div class="input-title">
          <div class="input-body">
            <input v-model="Newname" class="inp" placeholder="姓名" type="text">
            <input v-model="NewPhone" class='inp' maxlength="11" placeholder='手机号' type="text">
            <input v-model="NewProvince" class="inp" placeholder="省份" type="text">
            <input v-model="NewCity" class="inp" placeholder="城市" type="text">
            <input v-model="NewRegion" class="inp" placeholder="县区" type="text">
            <input v-model="NewDetail" class="inp" placeholder="详细地址" type="text">
          </div>
        </div>
        <div class="showbtn">
          <div class="btn" @click="close">关闭</div>
          <div class="btn now" @click='add'>添加</div>
        </div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'addressPage',
  data () {
    return {
      showPannel: false, // 添加地址弹窗
      Newname: '',
      NewPhone: '',
      NewProvince: '',
      NewCity: '',
      NewRegion: '',
      NewDetail: ''
    }
  },
  computed: {
    ...mapState('address', ['addressList']),
    isLogin () {
      return this.$store.getters.token
    }
  },
  methods: {
    // 获得地址列表
    getAddressList () {
      if (this.isLogin) {
        this.$store.dispatch('address/getAddressAction')
      }
    },
    // 请求删除地址
    async deleteAddress (addressId) {
      await this.$store.dispatch('address/deleteAddress', addressId)
      this.$router.go(0)
    },
    // 删除商品弹窗
    handleDel (addressId) {
      if (addressId) {
        this.$dialog.confirm({
          title: '删除地址',
          message: '是否确认删除该地址',
          confirmButtonText: '确认',
          cancelButtonText: '取消'
        }).then(() => {
          this.deleteAddress(addressId)
        }).catch(() => {})
      }
    },
    // 打开窗口
    addAddressWindow () {
      this.showPannel = true
    },
    // 关闭窗口
    close () {
      // 关闭弹窗
      this.showPannel = false
    },
    // 加入新地址
    add () {
      this.$store.dispatch('address/addAddressList', {
        name: this.Newname,
        phone: this.NewPhone,
        region: [
          {
            value: 782,
            label: this.NewProvince
          },
          {
            value: 783,
            label: this.NewCity
          },
          {
            value: 785,
            label: this.NewRegion
          }],
        detail: this.NewDetail
      })
      // 关闭弹窗
      this.showPannel = false
    },
    useAddress (addressId) {
      this.$store.dispatch('address/setDefAddress', addressId)
      this.$router.go(-1)
    }

  },
  created () {
    this.getAddressList()
  }
}
</script>

<style lang="less" scoped>
.address {
  padding-top: 0px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .address-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .add {
      .van-icon {
        font-size: 18px;
      }
    }
  }
  .address-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    background-color: #ffffff;
    border-radius: 5px;
    .top {
      display: flex;
      justify-content: space-between;
      padding: 10px;
      .name, .phone {
        font-size: 16px;
      }
    }
    .middle {
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      justify-content: space-between;
      .province, .city,
      .region {
        display: flex;
        align-items: flex-end;
        color: #000;
        font-size: 14px;
      }
    }
    .bottom {
      //width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      //flex-direction: column;
      justify-content: space-between;
      .detail {
        display: flex;
        align-items: flex-end;
        color: #000;
        font-size: 14px;
      }
      .delete{
        font-size: 14px;
      }
    }
  }
}
// 地址为空
.empty-address {
  padding: 80px 30px;
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
}
// 弹窗
.addressinp{
  .input-body {
  border-bottom: 1px solid #f3f1f2;
  padding: 8px;
  margin-bottom: 14px;
  align-items: center;
  .inp {
    display: block;
    border: none;
    outline: none;
    height: 32px;
    font-size: 14px;
    flex: 1;
  }
}
  .showbtn {
    display: flex;
    justify-content: space-between;
    .btn, .btn-none {
      height: 40px;
      width: 100px;
      line-height: 40px;
      margin: 20px;
      border-radius: 20px;
      text-align: center;
      color: rgb(255, 255, 255);
      background-color: rgb(255, 148, 2);
    }
    .btn.now {
      background-color: #fe5630;
    }
    .btn-none {
      background-color: #cccccc;
    }
  }
}
</style>
